.editor-tags-placeholder-container {
  padding: 0 $base-unit;

  .half-slide-previous.previous-slide & {
    display: none;
  }
}

.editor-tags-placeholder-content {
  display: flex;
  flex-wrap: wrap;
}

.editor-tags-placeholder {
  font-family: $serif-font-family;
  line-height: ms-line-height(0);
  margin-bottom: $base-unit - $base-unit * .5;

  .title {
    max-width: initial;
    text-align: left;
    margin-bottom: $base-unit * .5;
  }

  .node-selected {
    flex: 0 1 auto;
    display: initial;
    padding-right: $base-unit * .75;
    margin-bottom: $base-unit * .5;
    margin-right: $base-unit * .5;
    font-weight: normal;
    text-align: left;
    background-color: transparent;
    color: $text-color;
    border: 1px solid currentColor;
    cursor: default;

    &::before {
      color: currentColor;
    }
  }

  .node-close {
    display: none;
  }
}
