.media-audio {
  width: 100%;


  .media-player {
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    width: 100%;
    max-width: $base-unit * 20;
    padding: $base-unit;

    :last-child {
      margin-bottom: 0;
    }
  }

  .media-player-controls {
    flex: 0 1 $base-unit * 3;
  }

  .media-player-meta {
    flex: 1;
    // margin-bottom: $base-unit;
  }

  .media-player-progress {
    flex: 0 100%;
  }
}

.media-player-audio-normal {
  // padding-top: $base-unit * .75;
  // padding-bottom: $base-unit * 1.5;

  .media-player-progress {
    margin-bottom: $base-unit * .5;
  }

  .media-player-meta {
    padding-left: $base-unit * 0.5;
    margin-bottom: $base-unit;
  }
}

.media-player-audio-simple {
  box-shadow: $box-shadow-small;
  align-items: flex-end;

  .media-player-meta {
    flex: 0 100%;
    align-self: flex-start;
    margin-bottom: $base-unit * 0.5;
  }

  .media-player-title {
    font-size: ms(0);
    line-height: ms-line-height(-1);
    margin-bottom: 0;
  }

  .media-player-controls {
    flex: 0;
  }
  .media-player-progress {
    flex: 1 auto;
  }

  .media-player-time {
    justify-content: flex-end;
  }

  .media-player-button {
    width: 3vw;
    height: 3vw;
  }

  .media-player-canvas {
    display: none;
  }
}

.audio-wavesurfer {
  flex: 0 100%;
}
