.svgs {
  &.module-with-gallery {
    display: flex;
    justify-content: center;
  }
}

.svgs-container {
  display: flex;

  .module-with-gallery & {
    // TODO: was a quick fix, test it properly
    flex: 0 1 $editor-width;
  }

  &.is-reversed {
    flex-direction: column-reverse;
  }

  &.layout-vertical {
    justify-content: flex-start;

    .item {
      margin-bottom: $base-unit;
      width: 100%;
      max-width: $base-unit * 30;
    }
  }
}

svg[data-svg-target] {
  // fix for IE
  // position: absolute;
  // width: 100%;
  // height: 1px;
  // overflow: visible;
  cursor: pointer;

  // kreslen
  path {
    fill: none;
    stroke: yellow;
    stroke-width: 5;

    &.red {
      stroke: red;
    }

    &.blue {
      stroke: blue;
    }
  }

  image {
    width: 100%;
    height: 100%;
  }
}

foreignObject {
  overflow: visible;
}

.svg-category-label {
  font-size: ms(0);
  font-weight: bold;
  text-shadow: 0 0 2px white, 0 0 15px white;
}

.svg-category-label + .svg-textarea {
  margin-top: $base-unit * .6;
}

.svg-textarea {
  cursor: pointer;
  background: rgba(white, .85);
  border: 4px solid transparent;
  border-radius: $base-border-radius * 5;

  // border-top-left-radius: 0;
  box-shadow: none;
  font-size: ms(1);
  min-height: 23px;
  height: ms-line-height(3);
  padding: calc(#{$base-unit / 2} - 2px);
  resize: none;
  overflow: hidden;
  word-wrap: break-word;
  transition: border-color $base-duration $base-timing;

  &:hover {
    border-color: rgba($text-color, .38);
  }

  &:focus,
  .is-dragged & {
    border-color: $text-color;
    box-shadow: 0;
    outline: 0;
  }

  &:focus {
    cursor: initial;
  }

  &.is-wrong {
    background-color: red;
  }

  .svg-comic-narration & {
    border-bottom-left-radius: $base-border-radius;
    border-bottom-right-radius: $base-border-radius;
  }

  .svg-text.orientation-left & {
    transform: translateX(-100%);
  }
  .svg-text.orientation-top & {
    transform: translateY(-100%);
  }
  .svg-text.orientation-left.orientation-top & {
    border-bottom-right-radius: 0;
    transform: translate(-100%, -100%);
  }
  .svg-text.orientation-left.orientation-bottom & {
    border-top-right-radius: 0;
  }
  .svg-text.orientation-right.orientation-top & {
    border-bottom-left-radius: 0;
  }
  .svg-text.orientation-right.orientation-bottom & {
    border-top-left-radius: 0;
  }

}

.svg-circle {
  width: $svg-circle-size;
  height: $svg-circle-size;
  margin-top: $svg-circle-size / -2;
  margin-left: $svg-circle-size / -2;
  background-color: blue;
  border: 5px solid blue;
  border-radius: 50%;
  cursor: pointer;

  .icon-svg {
    display: none;
    width: $svg-circle-size;
    height: $svg-circle-size;

    svg {
      padding: ($svg-circle-size - $base-unit) / 2;
    }
  }

  &:hover {
    .icon-svg {
      display: block;
    }
  }

  &.red {
    color: red;
    background-color: rgba(red, .38);
    border-color: red;

    &:hover,
    .is-dragged & {
      background-color: red;
    }
  }

  &.blue {
    color: blue;
    background-color: rgba(blue, .38);
    border-color: blue;

    &:hover,
    .is-dragged & {
      background-color: blue;
    }
  }
}

.svg-circle-text {
  z-index: 1;
  color: $text-color;
  background-color: $text-color;
  border: 0;
  margin-top: 0;
  margin-left: 0;
  width: $svg-circle-size * .5;
  height: $svg-circle-size * .5;
  position: absolute;
  top: $svg-circle-size * -.25;
  left: $svg-circle-size * -.25;

  &.red {
    background-color: red;
  }

  &.blue {
    background-color: blue;
  }
}

.svg-path {
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
  will-change: stroke-width;
  transition: stroke-width $base-duration $base-timing;

  &:hover {
    stroke-width: 9;
  }
}

.is-dragged,
.is-dragged > * {
  cursor: -moz-grabbing !important;
  cursor: -webkit-grabbing !important;
  cursor: grabbing !important;
}

// SVG KOMIKS
// predefined comic bubbles

// .svg-komiks {}

.svg-text {
  &.top.left {
    .svg-textarea {
      border-top-left-radius: 0;
    }
  }

  &.top.right {
    .svg-textarea {
      border-top-right-radius: 0;
    }
  }

  &.bottom.right {
    .svg-textarea {
      border-bottom-right-radius: 0;
    }
  }

  &.bottom.left {
    .svg-textarea {
      border-bottom-left-radius: 0;
    }
  }
}

