$borderSettings: 1px solid desaturate(darken($bg-color, 10), 30);

.table-column {
  width: 25%;
  display: inline-block;
  margin-right: 5%;
}

.table-selected-items,
.table-result {
  min-height: $base-unit * 4;
}

.table-selected-items {
  min-height: $base-unit * 4;
  border-top-left-radius: $base-border-radius;
  border-top-right-radius: $base-border-radius;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;

  .table-body {
    padding: $base-unit / 2;
  }

  .node-selected {
    cursor: grab;
    padding: 0 ($base-unit * 0.25);

    .node-close {
      display: none;
    }
  }
}

.table-container {
  overflow: hidden;
  width: calc(100% - #{$base-unit * 2});
  margin: 0 auto;
  background: $bg-color-1;
  box-shadow: $box-shadow;
  border-radius: $base-border-radius;
  margin-bottom: $base-unit;

  @include mq($min-width: $screen-xl) {
    width: 80%;
    max-width: $base-unit * 54;

    .previous-slide & {
      width: calc(100% - #{$base-unit * 2});
    }
  }
}

.table {
  overflow: hidden;
  line-height: ms-line-height(0);
  border-bottom-left-radius: $base-border-radius;
  border-bottom-right-radius: $base-border-radius;

  .node-selected {
    line-height: ms-line-height(1);
    margin: $base-unit * 0.125;
    border: none;
  }

  .table-row {
    display: flex;
    background: $bg-color-1;
    border-bottom: $borderSettings;
    border-radius: 0;
    overflow: hidden;

    &:last-child {
      border-bottom: 0;
    }
  }

  .cell {
    flex: 1;
    padding: $base-unit * 0.5;

    &.table-input {
      padding: 0;
    }
  }

  .cell-border-right {
    border-right: $borderSettings;
  }

  .heading {
    background: desaturate(darken($bg-color, 10), 30);
    min-height: $base-unit * 2;
    padding: $base-unit * .5 0;
  }

  input[type="text"] {
    width: 100%;
    height: 100%;
    padding: 0 ($base-unit / 2);
    border: 0;
  }
}
