.slide-export {
  .nav-button {
    z-index: 9;
    line-height: $navigation-height;
    color: $bg-color-2;

    &:hover {
      color: $bg-color-1;

      .icon-svg {
        background-color: $bg-color-1;
        color: $text-color;
      }
    }
  }

  .overlay {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    height: 100%;

    &::before {
      content: "";
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.8);
    }
  }

  .row {
    position: relative;
    color: $bg-color-1;
    background-color: transparent;
    height: 100%;
  }

  .content {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
  }

  .zadani {
    > * {
      text-shadow: 0 1px 0.38em rgba($text-color, 0.62);
    }
  }

  h1 {
    font-weight: 700;
  }

  button[type="button"] {
    margin-bottom: $base-unit * 0.25;
  }

  button[data-nav-button="prev"] {
    margin-bottom: $base-unit * 3;
  }

  svg {
    fill: currentColor;
  }

  .tacr {
    color: $bg-color-2;
    position: absolute;
    bottom: $base-unit;
    right: $base-unit;
  }

  .tacr-logo {
    width: $base-unit * 4;
    height: $base-unit * 4;
  }
}
