// same as .text-option in _editor.text-option.scss
.node-selected {
  cursor: default;
  display: inline-block;
  position: relative;
  z-index: 1;
  padding: calc(#{($base-unit * 0.125)} - 1px) ($base-unit * 0.75) 0 ($base-unit * 0.25);
  border-radius: $base-border-radius + 1;
  border: 1px solid $bg-color;
  line-height: ms-line-height(-2);

  .previous-slide &,
  .cell-read-only & {
    padding: 0 ($base-unit * 0.25);

    .node-close {
      display: none;
    }
  }

  &.active {
    opacity: .5;
  }

  // TODO: add svg icon instead of Unicode ""
  .node-close {
    cursor: pointer;
    position: absolute;
    top: 50%;
    right: 0;
    display: inline-block;
    width: $base-unit * 0.65;
    height: $base-unit * 0.65;
    line-height: $base-unit * 0.65;
    font-family: $ui-font-family;
    font-size: ms(-1);
    color: currentColor;
    text-align: center;
    border-radius: 50%;
    transform: translateY(-50%);
    transform-origin: center;

    &:hover {
      top: calc(50% + 2px);
      color: $bg-color;
      background-color: $text-color;
      box-shadow: $button-shadow;
      transform: scale(1.2) translateY(-50%);

      &::before {
        opacity: 1;
      }
    }

    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      opacity: .62;
    }
  }

  // JI: Co je a kde se vyskytuje .status?
  .title {
    position: absolute;
    left: -60px;
    top: 25%;
    color: #000;
    transform: rotate(-90deg);
    display: inline-block;
  }
}

.has-comments {
  cursor: pointer;
}

.node-text-red {
  @extend .text-red !optional;
}

.node-text-blue {
  @extend .text-blue !optional;
}

.node-text-gray {
  @extend .text-gray !optional;
}

.node-text-orange {
  @extend .text-orange !optional;
}

.node-text-yellow {
  @extend .text-yellow !optional;
}

.node-text-green {
  @extend .text-green !optional;
}

.node-text-black {
  @extend .text-black !optional;
}

.dashboard .node-selected .node-close {
  display: none;
}
