$fullscreen-toggle-size: $base-unit * 1.25;

.fullscreen-toggle {
  position: absolute;
  top: $base-unit * 0.5;
  right: $base-unit * 0.5;
  width: $fullscreen-toggle-size;
  height: $fullscreen-toggle-size;

  background-color: rgba($text-color, 0.62);
  border: 1px solid transparent;
  border-radius: 50%;
  box-shadow: 0 0 ($base-unit * 0.5) rgba($text-color, 0.38);
  color: $bg-color;
  cursor: pointer;
  padding: 0;

  opacity: 0;
  transition: opacity 0.2s ease;

  &::after {
    display: none;
    content: "Zvtit";
    position: absolute;
    top: 50%;
    right: calc(100% + #{$base-unit * 0.25});
    transform: translateY(-50%);

    background-color: rgba($text-color, 0.62);
    border-radius: $base-border-radius;
    font-size: ms(-2);
    letter-spacing: 0.05em;
    line-height: ms-line-height(-3);
    padding: ($base-unit * 0.1875) ($base-unit * 0.25) ($base-unit * 0.125);
    text-align: center;
    text-transform: uppercase;
    text-shadow: 0 0 ($base-unit * 0.5) $text-color;
  }

  &:hover {
    border: 1px solid rgba($bg-color-1, 1);
    background-color: rgba($text-color, 0.9);
    color: $bg-color-1;

    &::after {
      display: block;
    }
  }

  .fullscreen & {
    &::after {
      content: "Zavt";
    }
  }

  // pointer-events: none;
  // will-change: opacity;

  body:not(.is-idle) *:hover > & {
    opacity: 1;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
    margin: 0 auto;
    width: $fullscreen-toggle-size * 0.5;
    height: $fullscreen-toggle-size * 0.5;
    fill: currentColor;
  }
}

.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba($text-color, 0.95);
  display: flex;
  z-index: $layer-fullscreen;
}

.fullscreen-container {
  flex: 1;
  width: 100%;
  height: 100%;
  padding: 0 ($base-unit * 2.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.fullscreen-image {
  box-shadow: $box-shadow;
}
