.module-with-gallery {
  justify-content: center;
}

.gallery-container {
  position: relative;
  position: sticky;
  top: $base-unit;
  flex: 0 1 $gallery-width;
  // max-height: 90vh;
  overflow-x: hidden;
  overflow-y: auto;
  overscroll-behavior: contain;
  margin-right: $base-unit * 2;
  margin-bottom: $base-unit;
  box-shadow: $box-shadow;
  border-radius: $base-border-radius;
  counter-reset: gallery;
  max-width: $gallery-width;
  align-self: flex-start;

  &.layout-gallery-big {
    flex: 0 1 $gallery-width-big;
    max-width: $gallery-width-big;
  }

  &.gallery-single {
    .gallery-item::before {
      display: none;
    }
  }

  &.gallery-multiple {
    max-height: 70vh;
  }

  .active-slide & {
    // min-width: 38%;
  }

  .half-slide-previous.previous-to-active-slide &,
  .half-slide-previous.previous-slide & {
    display: none;
  }
}

.gallery-item {
  position: relative;
  border-top: 1px solid $text-color;

  &:first-of-type {
    border-top: 0;
  }

  &::before {
    counter-increment: gallery;
    content: counter(gallery);
    position: absolute;
    top: $base-unit * 0.5;
    left: $base-unit * 0.5;
    width: $base-unit * .75;
    height: $base-unit * .75;
    line-height: $base-unit * .75;
    background-color: rgba($text-color, .62);
    border-radius: 50%;
    box-shadow: 0 0 $base-unit / 2 rgba($text-color, .62);
    color: $bg-color;
    font-family: $ui-font-family;
    font-feature-settings: "lnum";
    font-size: ms(-2);
    text-transform: uppercase;
    letter-spacing: .05em;
    z-index: 1;

    will-change: opacity;
    transition: opacity $base-duration $base-timing;
  }

  &:hover {
    &::before {
      opacity: .38;
    }
  }

  img {
    width: 100%;
  }
}
