.caption-on-hover {
  // display: none;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  padding: $base-unit * .5 $base-unit * .5 $base-unit * .25;
  background: linear-gradient(to top, rgba($text-color, .5) 0%, rgba($text-color, 0) 100%);
  background-position: center right;
  color: $bg-color-1;
  font-size: ms(-2);
  line-height: ms-line-height(-4);
  text-shadow: 0 0 $base-unit * .125 $text-color;
  overflow: visible;
  text-align: left;
  pointer-events: none;

  opacity: 1;
  will-change: opacity;
  transition: opacity $base-duration $base-timing;

  .gallery-item:hover & {
    // display: none;
    opacity: 0;
  }
}
