.test-quiz-container {
  .test-quiz-item {
    min-height: $base-unit * 3;
    display: flex;
    border-radius: 0;

    &:not(:last-of-type) {
      border-bottom: 1px solid currentColor;
    }

    .heading,
    .option {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 ($base-unit * 0.25);
    }

    .heading {
      flex: 2;
    }

    .option {
      flex: 1;

      &.selected,
      &:hover {
        color: currentColor;
        cursor: pointer;

        .tick {
          border-color: currentColor;
        }
      }

      &.selected {
        background-color: white;
      }

      .tick {
        border: 1px solid currentColor;
        border-radius: $base-unit;
        display: inline-block;
        width: $base-unit;
        height: $base-unit;
        vertical-align: middle;
        margin-right: $base-unit * 0.5;
        position: relative;
        line-height: $base-unit * 0.9;

        &:before {
          content: '\2713';
          opacity: 0;
          position: relative;
          font-size: ms(1);
        }
      }

      &:hover .tick:before,
      &.selected .tick:before {
        opacity: 1;
      }
    }
  }
}
